import React, { useState } from 'react'
import { useMetadata } from '../hooks/useMetadata'
import { api, TransactionCreate } from '../services/api'
import toast from 'react-hot-toast'

const TransactionPage: React.FC = () => {
  const { warehouses, costTypes, loading: metadataLoading, error: metadataError } = useMetadata()
  
  const [formData, setFormData] = useState<TransactionCreate>({
    date: new Date().toISOString().split('T')[0],
    warehouse_id: '',
    cost_type_id: '',
    is_income: false,
    amount: 0,
    currency: 'TJS'
  })
  
  const [submitting, setSubmitting] = useState(false)

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    
    if (!formData.warehouse_id || !formData.cost_type_id || formData.amount <= 0) {
      toast.error('Please fill in all required fields')
      return
    }

    try {
      setSubmitting(true)
      const response = await api.createTransaction(formData)
      
      if (response.error) {
        toast.error(response.error)
        return
      }
      
      toast.success('Transaction created successfully!')
      
      // Reset form
      setFormData({
        date: new Date().toISOString().split('T')[0],
        warehouse_id: '',
        cost_type_id: '',
        is_income: false,
        amount: 0,
        currency: 'TJS'
      })
    } catch (error) {
      toast.error('Failed to create transaction')
    } finally {
      setSubmitting(false)
    }
  }

  const handleInputChange = (field: keyof TransactionCreate, value: any) => {
    setFormData(prev => ({
      ...prev,
      [field]: value
    }))
  }

  if (metadataLoading) {
    return (
      <div className="space-y-6">
        <div>
          <h1 className="text-2xl font-bold text-gray-900">New Transaction</h1>
          <p className="text-gray-600">Add a new income or expense transaction</p>
        </div>
        <div className="card max-w-2xl">
          <div className="flex items-center justify-center py-12">
            <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-primary-600"></div>
            <span className="ml-2 text-gray-600">Loading...</span>
          </div>
        </div>
      </div>
    )
  }

  if (metadataError) {
    return (
      <div className="space-y-6">
        <div>
          <h1 className="text-2xl font-bold text-gray-900">New Transaction</h1>
          <p className="text-gray-600">Add a new income or expense transaction</p>
        </div>
        <div className="card max-w-2xl">
          <div className="text-center py-12">
            <p className="text-red-600">Error loading data: {metadataError}</p>
            <button 
              onClick={() => window.location.reload()}
              className="mt-4 btn btn-primary"
            >
              Retry
            </button>
          </div>
        </div>
      </div>
    )
  }

  return (
    <div className="space-y-6">
      <div>
        <h1 className="text-2xl font-bold text-gray-900">New Transaction</h1>
        <p className="text-gray-600">Add a new income or expense transaction</p>
      </div>
      
      <div className="card max-w-2xl">
        <form onSubmit={handleSubmit} className="space-y-6">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            <div>
              <label className="block text-sm font-medium text-gray-700">
                Warehouse *
              </label>
              <select 
                className="input mt-1"
                value={formData.warehouse_id}
                onChange={(e) => handleInputChange('warehouse_id', e.target.value)}
                required
              >
                <option value="">Select warehouse...</option>
                {warehouses.map(warehouse => (
                  <option key={warehouse.id} value={warehouse.id}>
                    {warehouse.emoji} {warehouse.name}
                  </option>
                ))}
              </select>
            </div>
            
            <div>
              <label className="block text-sm font-medium text-gray-700">
                Transaction Type *
              </label>
              <select 
                className="input mt-1"
                value={formData.cost_type_id}
                onChange={(e) => handleInputChange('cost_type_id', e.target.value)}
                required
              >
                <option value="">Select type...</option>
                {costTypes.map(costType => (
                  <option key={costType.id} value={costType.id}>
                    {costType.name} {costType.is_direct ? '(Direct)' : '(Indirect)'}
                  </option>
                ))}
              </select>
            </div>
          </div>
          
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            <div>
              <label className="block text-sm font-medium text-gray-700">
                Amount *
              </label>
              <input 
                type="number" 
                className="input mt-1" 
                placeholder="0.00"
                value={formData.amount}
                onChange={(e) => handleInputChange('amount', parseFloat(e.target.value) || 0)}
                min="0"
                step="0.01"
                required
              />
            </div>
            
            <div>
              <label className="block text-sm font-medium text-gray-700">
                Currency
              </label>
              <select 
                className="input mt-1"
                value={formData.currency}
                onChange={(e) => handleInputChange('currency', e.target.value)}
              >
                <option value="TJS">TJS</option>
                <option value="USD">USD</option>
                <option value="EUR">EUR</option>
              </select>
            </div>
          </div>
          
          <div>
            <label className="block text-sm font-medium text-gray-700">
              Date
            </label>
            <input 
              type="date" 
              className="input mt-1"
              value={formData.date}
              onChange={(e) => handleInputChange('date', e.target.value)}
            />
          </div>

          <div>
            <label className="flex items-center">
              <input
                type="checkbox"
                className="rounded border-gray-300 text-primary-600 shadow-sm focus:border-primary-300 focus:ring focus:ring-primary-200 focus:ring-opacity-50"
                checked={formData.is_income}
                onChange={(e) => handleInputChange('is_income', e.target.checked)}
              />
              <span className="ml-2 text-sm text-gray-700">This is income (not expense)</span>
            </label>
          </div>
          
          <div className="flex justify-end">
            <button 
              type="submit" 
              className="btn btn-primary"
              disabled={submitting}
            >
              {submitting ? (
                <div className="flex items-center">
                  <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white mr-2"></div>
                  Saving...
                </div>
              ) : (
                'Save Transaction'
              )}
            </button>
          </div>
        </form>
      </div>
    </div>
  )
}

export default TransactionPage 